import { Module } from "@nestjs/common";
import { ConfigModule } from "@nestjs/config";
import { CryptoAuthMicroServiceStrategy } from "../jwt/strategy/crypto-auth-micro-service.strategy";
import { NatMicroServiceModule } from "../nat-service";
import { CryptoAuthMicroServiceGuard } from "./guards/crypto-auth-micro-service.guard";

@Module({  
  imports: [
    NatMicroServiceModule
  ],
  providers: [
    {
      provide: "APP_GUARD",
      useClass: CryptoAuthMicroServiceGuard,
    },
    CryptoAuthMicroServiceStrategy,
    ConfigModule
  ],
  exports: [
    // 'NAT_SERVICE',
    CryptoAuthMicroServiceStrategy,
  ]
})
export class CryptoAuthMicroServiceModule {}